import React from 'react';
import { Container } from '@mui/material';
import GlobalAssetManagement from '../components/GlobalAssetManagement';
import { useGlobalAssets } from '../hooks/useGlobalAssets';
import { useMarketDataStats, useMarketDataProviders, useRecentUpdates } from '../hooks/useMarketData';


const GlobalAssetsPage: React.FC = () => {
  // Use real API hooks
  const { 
    data: assets = [], 
    isLoading: assetsLoading, 
    error: assetsError,
    refetch: refetchAssets 
  } = useGlobalAssets();

  const { 
    data: marketDataStats, 
    isLoading: statsLoading, 
    error: statsError 
  } = useMarketDataStats();

  const { 
    data: providers = [], 
    isLoading: providersLoading, 
    error: providersError 
  } = useMarketDataProviders();

  const { 
    data: recentUpdates = [], 
    isLoading: updatesLoading, 
    error: updatesError 
  } = useRecentUpdates(50);

  // API handlers using real hooks
  const handleRefresh = async () => {
    await refetchAssets();
  };

  const handleCreateAsset = async (data: any) => {
    // This will be handled by the GlobalAssetManagement component
    console.log('Creating asset:', data);
  };

  const handleUpdateAsset = async (id: string, data: any) => {
    // This will be handled by the GlobalAssetManagement component
    console.log('Updating asset:', id, data);
  };

  const handleDeleteAsset = async (id: string) => {
    // This will be handled by the GlobalAssetManagement component
    console.log('Deleting asset:', id);
  };

  const handlePriceUpdate = async (
    assetId: string, 
    price: number, 
    priceType: string, 
    priceSource: string, 
    changeReason?: string
  ) => {
    // This will be handled by the AssetPriceManagement component
    console.log('Updating price:', { assetId, price, priceType, priceSource, changeReason });
  };

  const handlePriceHistoryRefresh = async (assetId: string) => {
    // This will be handled by the AssetPriceManagement component
    console.log('Refreshing price history for asset:', assetId);
  };

  const handleMarketDataRefresh = async () => {
    // This will be handled by the MarketDataDashboard component
    console.log('Refreshing market data...');
  };

  const handleUpdateAllPrices = async () => {
    // This will be handled by the MarketDataDashboard component
    console.log('Updating all prices...');
  };

  const handleUpdateByNation = async (nation: string) => {
    // This will be handled by the MarketDataDashboard component
    console.log('Updating prices by nation:', nation);
  };

  const handleUpdateByMarket = async (marketCode: string) => {
    // This will be handled by the MarketDataDashboard component
    console.log('Updating prices by market:', marketCode);
  };

  const handleTestProvider = async (providerName: string) => {
    // This will be handled by the MarketDataDashboard component
    console.log('Testing provider:', providerName);
  };

  const handleUpdateConfig = async (config: any) => {
    // This will be handled by the MarketDataDashboard component
    console.log('Updating config:', config);
  };

  // Combine loading states
  const isLoading = assetsLoading || statsLoading || providersLoading || updatesLoading;
  
  // Combine error states
  const error = assetsError || statsError || providersError || updatesError;
  const errorMessage = error ? (error as Error)?.message || String(error) : undefined;

  return (
    <Container maxWidth="xl" sx={{ py: 3 }}>
      <GlobalAssetManagement
        assets={assets}
        loading={isLoading}
        error={errorMessage}
        onRefresh={handleRefresh}
        onCreateAsset={handleCreateAsset}
        onUpdateAsset={handleUpdateAsset}
        onDeleteAsset={handleDeleteAsset}
        onPriceUpdate={handlePriceUpdate}
        onPriceHistoryRefresh={handlePriceHistoryRefresh}
        marketDataStats={marketDataStats}
        marketDataProviders={providers}
        recentUpdates={recentUpdates}
        onMarketDataRefresh={handleMarketDataRefresh}
        onUpdateAllPrices={handleUpdateAllPrices}
        onUpdateByNation={handleUpdateByNation}
        onUpdateByMarket={handleUpdateByMarket}
        onTestProvider={handleTestProvider}
        onUpdateConfig={handleUpdateConfig}
      />
    </Container>
  );
};

export default GlobalAssetsPage;
